import * as React from "react"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { graphql } from "gatsby"
import Layout from "../components/layout"
import SeoBusiness from "../components/seoBusiness"

const AboutPage = ({data}) => {
  const pageInfo = data.allMarkdownRemark.nodes[0]
  return (
  <Layout>
    <SeoBusiness title="Home" />
    <section className="page-section about-heading">
      <div className="container">
         <GatsbyImage
            className="img-fluid rounded about-heading-img mb-3 mb-lg-0 custom-box-shadow"
            image={getImage(pageInfo.frontmatter.headerImage)}
            quality={95}
            placeholder="blurred"
            formats={["auto", "webp", "avif"]}
            alt="sunburst store image"
          />
        <div className="about-heading-content">
          <div className="row">
            <div className="col-xl-9 col-lg-10 mx-auto">
              <div className="bg-faded rounded p-5">
                <h2 className="section-heading mb-4">
                  <span className="section-heading-upper">
                  {pageInfo.frontmatter.subtitle}
                  </span>
                  <span className="section-heading-lower">{pageInfo.frontmatter.title}</span>
                </h2>
                <section
                dangerouslySetInnerHTML={{ __html: pageInfo.html }}
              />
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </Layout>
)
  }

export default AboutPage

export const pageQuery = graphql`
  query AboutPage {
    allMarkdownRemark(
      filter: {frontmatter: {type: {eq: "data"}, for: {eq: "about"}}}
    ) {
      nodes {
        id
        frontmatter {
          title
          subtitle
          headerImage {
            childImageSharp {
              gatsbyImageData
            }
          }
        }
        html
      }
    }
  }
`